<template>
  <div class="manage">
    <el-dialog
      title="Êñ∞Â¢ûÁî®Êà∑"
      :visible.sync="dialogVisible"
      width="40%"
      :beforeClose="handleClose"
    >
      <el-form
        ref="form"
        :model="form"
        label-width="80px"
        :inline="true"
        :rules="rules"
      >
        <el-form-item label="ÂßìÂêç" prop="name">
          <el-input v-model="form.name" placeholder="ËØ∑ËæìÂÖ•ÂßìÂêç"></el-input>
        </el-form-item>
        <el-form-item label=" Âπ¥ÈæÑ" prop="age">
          <el-input v-model="form.age" placeholder="ËØ∑ËæìÂÖ•Âπ¥ÈæÑ"></el-input>
        </el-form-item>
        <el-form-item label="ÊÄßÂà´" prop="sex">
          <el-select v-model="form.sex" placeholder="ËØ∑ÈÄâÊã©ÊÄßÂà´">
            <el-option label="Áî∑" :value="1"></el-option>
            <el-option label="Â•≥" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Âá∫ÁîüÊó•Êúü" prop="birth">
          <el-col :span="15">
            <el-date-picker
              type="date"
              placeholder="ÈÄâÊã©Êó•Êúü"
              v-model="form.birth"
              value-format="yyyy-MM-dd"
              style="width: 100%"
            ></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label=" Âú∞ÂùÄ" prop="addr">
          <el-input v-model="form.addr" placeholder="ËØ∑ËæìÂÖ•Âú∞ÂùÄ"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="submitForm">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>
    <div class="manage-header">
      <el-button @click="handleAdd" type="primary"> + Êñ∞Â¢û </el-button>
      <!-- formÊêúÁ¥¢Âå∫Âüü -->
      <el-form :model="userForm" :inline="true">
        <el-form-item>
          <el-input  placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑ÂêçÁß∞" v-model="userForm.name"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">Êü•ËØ¢</el-button>
        </el-form-item>

      </el-form>
    </div>
    <div class="common-table">
      <el-table :data="tableData" style="width: 100%" height="600px" stripe>
        <el-table-column prop="name" label="ÂßìÂêç"> </el-table-column>
        <el-table-column prop="age" label="Âπ¥ÈæÑ"> </el-table-column>
        <el-table-column prop="sex" label="ÊÄßÂà´">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{
              scope.row.sex === 1 ? "Áî∑" : "Â•≥"
            }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="birth" label="Âá∫ÁîüÊó•Êúü"> </el-table-column>
        <el-table-column prop="addr" label="Âú∞ÂùÄ"> </el-table-column>
        <el-table-column prop="" label="Êìç‰Ωú">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)"
              >ÁºñËæë</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row)"
              >Âà†Èô§</el-button
            >
          </template> </el-table-column
        >` `
      </el-table>
      <!-- ÂàÜÈ°µ -->
      <div class="pager">
        <el-pagination
          layout="prev, pager, next"
          :total="total"
          @current-change="handlePageChange"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { getUser, addUser, editUser, deleteUser } from "../api";
export default {
  name: "MyUser",

  data() {
    return {
      dialogVisible: false,
      form: {
        name: "",
        age: "",
        sex: "",
        birth: "",
        addr: "",
      },
      rules: {
        name: [{ required: true, message: "ËØ∑ËæìÂÖ•ÂßìÂêç", trigger: "blur" }],
        age: [{ required: true, message: "ËØ∑ËæìÂÖ•Âπ¥ÈæÑ", trigger: "blur" }],
        sex: [{ required: true, message: "ËØ∑ÈÄâÊã©ÊÄßÂà´", trigger: "blur" }],
        birth: [{ required: true, message: "ËØ∑ÈÄâÊã©Âá∫ÁîüÊó•Êúü", trigger: "blur" }],
        addr: [{ required: true, message: "ËØ∑ËæìÂÖ•Âú∞ÂùÄ", trigger: "blur" }],
      },
      tableData: [
        // { birth: "2021-01-01", name: "Âº†‰∏â", addr: "Âåó‰∫¨Â∏ÇÊµ∑Ê∑ÄÂå∫" },
      ],
      modelType: 0, //0Êñ∞Â¢ûÔºå1ÁºñËæë
      total: 0, //ÂΩìÂâçtableÊï∞ÊçÆÊÄªÊù°Êï∞
      pageData: {
        page: 1,
        limit: 10,
      },
      userForm: {
        name: "",
      },
    };
  },

  mounted() {
    //È°µÈù¢È¶ñÊ¨°Âä†ËΩΩË∞ÉÁî®Ëé∑ÂèñÁî®Êà∑ÂàóË°®Êï∞ÊçÆ
    this.getList();
  },

  methods: {
    //Êèê‰∫§Áî®Êà∑Ë°®Âçï
    submitForm() {
      this.$refs.form.validate((valid) => {
        //valid‰∏∫trueÊó∂ÊâçËÉΩÁªßÁª≠ÂØπË°®ÂçïÊï∞ÊçÆËøõË°åÂ§ÑÁêÜ
        //ÂêéÁª≠ÂØπË°®ÂçïÊï∞ÊçÆÁöÑÂ§ÑÁêÜ
        if (valid) {
          if (this.modelType === 0) {
            //üëâÊñ∞Â¢ûÁî®Êà∑
            addUser(this.form).then(() => {
              //Êñ∞Â¢ûÊàêÂäüÂêéÈáçÊñ∞Ëé∑ÂèñÁî®Êà∑ÂàóË°®Êï∞ÊçÆ
              this.getList();
            });
          } else {
            //üëâÁºñËæëÁî®Êà∑
            editUser(this.form).then(() => {
              //ÁºñËæëÊàêÂäüÂêéÈáçÊñ∞Ëé∑ÂèñÁî®Êà∑ÂàóË°®Êï∞ÊçÆ
              this.getList();
            });
          }
          //Êèê‰∫§Ë°®ÂçïÊï∞ÊçÆ
          // console.log(this.form, "form");
          //ÂÖ≥Èó≠ÂºπÁ™ó‰πãÂâçÂÖàÈáçÁΩÆË°®Âçï
          this.$refs.form.resetFields();
          //ÂÖ≥Èó≠ÂºπÁ™ó
          this.dialogVisible = false;
        }
      });
    },
    handleClose() {
      //ÂÖ≥Èó≠ÂºπÁ™óÊó∂ÈáçÁΩÆË°®Âçï
      this.$refs.form.resetFields();
      //ÂÖ≥Èó≠ÂºπÁ™ó
      this.dialogVisible = false;
    },
    getList() {
      //Ëé∑ÂèñÂàóË°®ÁöÑÊï∞ÊçÆ
      getUser({ params: { ...this.userForm, ...this.pageData } }).then(
        (data) => {
          // console.log(data.data.count);
          this.tableData = data.data.list;
          this.total = data.data.count || 0;
        }
      );
    },
    handleAdd() {
      this.modelType = 0;
      //ÁÇπÂáªÊñ∞Â¢ûÊåâÈíÆÊó∂ÔºåÊâìÂºÄÊñ∞Â¢ûÂºπÁ™ó
      this.dialogVisible = true;
    },
    handleEdit(row) {
      //üëâÁºñËæëÁî®Êà∑
      this.modelType = 1;
      this.dialogVisible = true;
      //üëâüëâÊ≥®ÊÑèÈúÄË¶ÅÂØπÂΩìÂâçË°åÊï∞ÊçÆËøõË°åÊ∑±Êã∑Ë¥ùÔºåÂê¶Âàô‰ºöÂá∫Èîô
      this.form = JSON.parse(JSON.stringify(row));
      //ÂÖ≥Èó≠ÂºπÁ™ó‰πãÂâçÂÖàÈáçÁΩÆË°®Âçï
      this.$refs.form.resetFields();
    },
    handleDelete(row) {
      this.$confirm("Ê≠§Êìç‰ΩúÂ∞ÜÊ∞∏‰πÖÂà†Èô§ËØ•Êñá‰ª∂, ÊòØÂê¶ÁªßÁª≠?", "ÊèêÁ§∫", {
        confirmButtonText: "Á°ÆÂÆö",
        cancelButtonText: "ÂèñÊ∂à",
        type: "warning",
      })
        .then(() => {
          deleteUser({ id: row.id }).then(() => {
            //Âà†Èô§ÊàêÂäüÂêéÈáçÊñ∞Ëé∑ÂèñÁî®Êà∑ÂàóË°®Êï∞ÊçÆ
            this.getList();
          });
          this.$message({
            type: "success",
            message: "Âà†Èô§ÊàêÂäü!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "Â∑≤ÂèñÊ∂àÂà†Èô§",
          });
        });
    },
    //ÈÄâÊã©È°µÁ†ÅÁöÑÂõûË∞ÉÂáΩÊï∞
    handlePageChange(val) {
      console.log(val, "È°µÁ†Å");
      this.pageData.page = val;
      this.getList(); //ÈáçÊñ∞Ëé∑ÂèñÁî®Êà∑ÂàóË°®Êï∞ÊçÆ
    },
    //ÊêúÁ¥¢Áî®Êà∑ÁöÑÂõûË∞ÉÂáΩÊï∞
    onSubmit(){
      this.getList(); //ÈáçÊñ∞Ëé∑ÂèñÁî®Êà∑ÂàóË°®Êï∞ÊçÆ
    }
  },
};
</script>
<style lang="less" scoped>
.manage {
  height: 90%;
  .manage-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .common-table {
    position: relative;
    height: calc(100% - 62px);
    .pager {
      position: absolute;
      bottom: 0;
      right: 20px;
    }
  }
}
</style>
